import React, {Component, useState} from 'react';
import { MenuProps } from 'antd';
import { Menu } from 'antd';

type MenuItem = Required<MenuProps>['items'][number];

function getItem(
    label: React.ReactNode,
    key?: React.Key | null,
    children?: MenuItem[],
    type?: 'group',
): MenuItem {
    return {
        key,
        children,
        label,
        type,
    };
}

const items: MenuItem[] = [
    getItem('.Net', 'sub1',[ //<MailOutlined />, [
        getItem('Option 1', '1'),
        getItem('Option 2', '2'),
        getItem('Option 3', '3'),
        getItem('Option 4', '4'),
    ]),

    getItem('RXJS', 'sub2',[ //<AppstoreOutlined />, [
        getItem('Option 5', '5'),
        getItem('Option 6', '6'),
        getItem('Submenu', '7'),
    ]),
    getItem('Angular', 'sub3',[ //<SettingOutlined />, [
        getItem('Option 9', '8'),
        getItem('Option 10', '9'),
        getItem('Option 11', '10'),
        getItem('Option 12', '11'),
    ]),
    getItem('ReactJS', 'sub4',[ //<SettingOutlined />, [
        getItem('Option 9', '12'),
        getItem('Option 10', '13'),
        getItem('Option 11', '14'),
        getItem('Option 12', '15'),
    ]),
];

class SidebarRight extends Component {
    render() {
        // const [theme, setTheme] = React.useState<MenuTheme>('dark');
        // const [current, setCurrent] = React.useState('1');


        const onClick: MenuProps['onClick'] = (e) => {
            console.log('click ', e);
        };
        return (
            <div className={"w-1/6 p-2 mr-2 shadow h-max mt-2"}>
                    <Menu
                        theme={'light'}
                        onClick={onClick}
                        style={{ width: '100%' }}
                        defaultOpenKeys={['sub1']}
                        selectedKeys={[1]}
                        mode="inline"
                        items={items}
                    />
            </div>
        );
    }
}

export default SidebarRight;